import React, { useMemo, useState } from 'react'
import { getQuarter } from 'date-fns'

import Flex from 'components/Flex'
import Text from 'components/Text'
import Box from 'components/Box'

const useQuarterControl = () => {
  const [startQuarter, setStartQurarter] = useState(() => getQuarter(new Date()))
  const quarterControl = useMemo(() => (
    <Flex as="label" my="1em" alignItems="center">
      <Text mr="0.5em">起始季度</Text>
      <Box width="6em">
        <Box
          as="input"
          maxWidth="100%"
          type="number"
          lineHeight="2"
          value={startQuarter}
          max={4}
          min={1}
          onChange={e => setStartQurarter(+e.target.value)}
        />
      </Box>
    </Flex>
  ), [startQuarter])
  return {
    startQuarter,
    quarterControl,
  }
}

export default useQuarterControl
